    overflow: auto;
}
.overflow-clip {
    overflow: clip;
}

.no-scrollbar {
    scrollbar-width: none;
}

.w-16 {
    width: calc(0.25rem * 16);
}
.h-16 {
    height: calc(0.25rem * 16);
}
.h-25 {
    height: calc(0.25rem * 25);
}
.h-screen {  	
    height: 100vh;
}

.relative {
    position: relative;
}
.absolute {
    position: absolute;
}

.flex {
    display: flex;
}

.flex-col {
    flex-direction: column;
}

.flex-auto {
    flex: 1 1 auto;
}

.flex-none {
    flex: none;
}

.grid {
    display: grid;
}
.auto-cols-1fr {
    grid-auto-columns: 1fr;
}
.grid-flow-col {
    grid-auto-flow: column;
}

.place-content-center {
    place-content: center;
}

.inset-0 {
    inset: 0;
}

.p-4 {
    padding: 1rem;
}

.gap-1 {
    gap: 0.25rem;
}
.gap-4 {
    gap: 1rem;
}

.text-xl {
    font-size: 1.25rem;
}
.text-4xl {
    font-size: 2.25rem;
}

.text-left {
    text-align: left;
}

.font-bold {
    font-weight: 700;
}

.line-through {
    text-decoration-line: line-through;
}

.bg-white {
    background-color: white;
}
.bg-black {
    background-color: black;
}

.bg-yellow {
    background-color: var(--yellow);
}
.bg-yellow-dark {
    background-color: var(--yellow-dark);
}
.bg-yellow-light {
    background-color: var(--yellow-light);
}

.text-yellow-dark {
    color: var(--yellow-dark);
}
.text-yellow-light {
    color: var(--yellow-light);
}


.border-2 {
    border-width: 2px;
}

.border-4 {
    border-width: 4px;
}

.border-black {
    border-color: black;
}

.border-solid {
    border-style: solid;
}

.rounded-full {
    border-radius: 9999px;
}
.rounded-sm {
    border-radius: 0.25rem;
}

