    filter: drop-shadow(8px 8px 0 currentColor); /* box-shadow clips on list */
}

.cursor-pointer {
    cursor: pointer;
}

.pointer-events-none {
    pointer-events: none;
}

.appearance-none {
    appearance: none;
}

.darkenonhover:hover {
    filter: brightness(0.9);
